import { apiService as api } from "app/store/apiService";
import { IShift, IShiftEssentials, ITableShifts, ITableShiftsArg } from "./ShiftsModel";

export const addTagTypes = ["shifts/fetchAll", "shifts/fetchOne"] as const;

const ShiftsApi = api.enhanceEndpoints({ addTagTypes }).injectEndpoints({
	endpoints: (build) => ({
		getAllShifts: build.query<GetShiftsApiResponse, GetShiftsApiArg>({
			query: ({ businessId, page, pageSize, sortBy, sortOrder, filter, ...other }) => {
				let params = "?";

				if (page) params += `page=${page}&`;
				if (pageSize) params += `pageSize=${pageSize}&`;
				if (sortBy) params += `sortBy=${sortBy}&`;
				if (sortOrder) params += `sortOrder=${sortOrder}&`;
				if (filter) params += `filter=${filter}&`;

				Object.keys(other).forEach((key) => {
					if (other[key]) {
						params += `${key}=${other[key]}&`;
					}
				});
				return {
					url: `/shifts/getshifts/${businessId}${params}`
				};
			},
			providesTags: ["shifts/fetchAll"]
		}),
		getOneShift: build.query<GetShiftApiResponse, GetShiftApiArg>({
			query: (shiftsId) => ({
				url: `/shifts/getshiftsbyid/${shiftsId}`
			}),
			providesTags: ["shifts/fetchOne"]
		}),
		postShift: build.mutation<PostShiftApiResponse, PostShiftApiArg>({
			query: (queryArg) => ({
				url: `/shifts`,
				method: "POST",
				data: queryArg
			}),
			invalidatesTags: ["shifts/fetchAll"]
		}),
		putShift: build.mutation<PutShiftApiResponse, PutShiftApiArg>({
			query: (queryArg) => ({
				url: `/shifts`,
				method: "PUT",
				data: queryArg
			}),
			invalidatesTags: ["shifts/fetchAll", "shifts/fetchOne"]
		}),
		deleteShift: build.mutation<void, DeleteShiftApiArg>({
			query: (shiftsId) => ({
				url: `/shifts/deleteshifts/${shiftsId}`,
				method: "DELETE"
			}),
			invalidatesTags: ["shifts/fetchAll"]
		})
	})
});

export default ShiftsApi;

export type GetShiftsApiResponse = ITableShifts;
export type GetShiftsApiArg = ITableShiftsArg;
export type GetShiftApiResponse = IShift;
export type GetShiftApiArg = string;
export type PostShiftApiResponse = boolean;
export type PostShiftApiArg = IShiftEssentials;
export type PutShiftApiResponse = boolean;
export type PutShiftApiArg = IShift;
export type DeleteShiftApiArg = string;

export const { useGetAllShiftsQuery, useGetOneShiftQuery, usePostShiftMutation, usePutShiftMutation, useDeleteShiftMutation } = ShiftsApi;

export type ShiftsApiType = { [ShiftsApi.reducerPath]: ReturnType<typeof ShiftsApi.reducer> };
